const CHECKING_ANSWER = 'CHECKING_ANSWER'

export const ChekingAnswerAC = (id, answer) => ({ type: CHECKING_ANSWER, id, answer })

let initialState = {
    questions: [{ id: 1, question: '–ß–µ–º –º–µ–Ω—å—à–µ –≤–æ–¥—ã, —Ç–µ–º –±–æ–ª—å—à–µ –≥–ª—É–±–∏–Ω–∞. –ß—Ç–æ —ç—Ç–æ?', answer: '–ª–∏—Ç–µ—Ä–∞—Ç—É—Ä–∞', answerUser: '' },
    { id: 2, question: '–ù–∞–∫–æ—Ä–º–∏—à—å ‚Äì –∂–∏–≤–µ—Ç, –Ω–∞–ø–æ–∏—à—å ‚Äì —É–º—Ä–µ—Ç. –û —á–µ–º —Ä–µ—á—å?', answer: '–æ–≥–æ–Ω—å', answerUser: '' },
    { id: 3, question: '–í–∞–º –¥–∞–ª–∏, –≤–∞–º –ø—Ä–∏–Ω–∞–¥–ª–µ–∂–∏—Ç. –í—ã –Ω–µ –ø—Ä–æ–¥–∞–≤–∞–ª–∏, –∞ –ø–æ–ª—å–∑—É—é—Ç—Å—è –≤—Å–µ –∑–Ω–∞–∫–æ–º—ã–µ.', answer: '–∏–º—è', answerUser: '' }],
    answersGiven: false
}

const testReducer = (state = initialState, action) => {
    let stateCopy;
    switch (action.type) {
        case CHECKING_ANSWER:
            let testAnswersGiven;
            let arr = state.questions.map(item => {
                if (action.id == item.id) {
                    item.answerUser = action.answer
                }
                item.answerUser == '' ? testAnswersGiven = false : testAnswersGiven = true;
                return item
            })
            console.log(arr)
            stateCopy = {
                ...state,
                questions: [...state.questions],
                answersGiven: testAnswersGiven
            }
            return stateCopy
        default:
            return state

    }
}

export default testReducer