import React, { useState } from 'react'
import styles from './Question.module.css'

const Question = ({ id, question, answer, answerUser, chekingAnswer, testDone }) => {

    const [answerValue, setAnswerValue] = useState('');
    const [error, setError] = useState(false)

    const onAnswer = (e) => {
        if (/^\s+$/.test(e.target.value) || e.target.value == '') {
            setError(true)
        } else {
            setAnswerValue(e.target.value.toLowerCase().trim())
            setError(false)
        }
    }

    const onChekingAnswer = () => {
        if (/^\s+$/.test(answerValue) || answerValue == '') {
            setError(true)
        } else {
            chekingAnswer(id, answerValue)
            setError(false)
        }
    }

    return (
        <div className={styles.container}>
            <div className={styles.question}>
                {question}
            </div>

            <div className={styles.navigation}>
                {testDone ?
                    <div>
                        <p>{answer == answerUser ?
                            <p className={styles.correctAnswer}>–í–∞—à –æ—Ç–≤–µ—Ç: <span>{answerUser}</span> - –ø—Ä–∞–≤–∏–ª—å–Ω–æ! </p> :
                            <p className={styles.unCorrectAnswer}>–í–∞—à –æ—Ç–≤–µ—Ç: <span>{answerUser}</span> - –Ω–µ –≤–µ—Ä–Ω–æ! –í–µ—Ä–Ω—ã–π –æ—Ç–≤–µ—Ç: <span>{answer}</span> </p>}</p>
                    </div> :
                    <div className={styles.dataInput}>
                        <input type="text" name="" id="" className={styles.answer} onChange={onAnswer} disabled={answerUser == '' ? false : true} />
                        <div className={`${styles.error} ${error ? styles.showError : ''}`}> –≠—Ç–æ –ø–æ–ª–µ –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –ø—É—Å—Ç—ã–º </div>
                        {answerUser == '' ?
                            <button onClick={onChekingAnswer} className={styles.button} disabled={error ? true : false}>–û—Ç–≤–µ—Ç–∏—Ç—å</button> :
                            <span className={styles.recd}>–û—Ç–≤–µ—Ç –ø—Ä–∏–Ω—è—Ç</span>}
                    </div>}
                <div className={styles.border}></div>
            </div>
        </div>
    )
}

export default Question